.navbar {
  background-color: #282c34;
  color: white;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 10px;
}

.nav-links li {
  margin: 0;
}

.nav-links a, .nav-links button {
  color: white;
  text-decoration: none;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
}

.nav-links button:hover, .nav-links a:hover {
  text-decoration: underline;
}

/* Modal Styles */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 20px;
  width: 300px;
  border-radius: 5px;
  position: relative;
}

.modal-content h2 {
  margin-top: 0;
}

.modal-content input {
  width: 100%;
  margin: 5px 0;
  padding: 8px;
  box-sizing: border-box;
}

.modal-content button[type="submit"] {
  width: 100%;
  padding: 8px;
  margin-top: 10px;
  background-color: #282c34;
  color: white;
  border: none;
  cursor: pointer;
}

.modal-content button[type="submit"]:hover {
  background-color: #444;
}

/* Close button */
.close-btn {
  position: absolute;
  top: 5px;
  right: 10px;
  background: none;
  border: none;
  font-size: 25px;
  font-weight: bold;
  cursor: pointer;
  color: #333;
}
